@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Epilogue:wght@400;500;600&family=Exo+2:wght@600&family=Josefin+Sans:wght@100;700&family=Outfit:wght@600;700&display=swap');

:root {
  --light-background:  rgb(201, 241, 248); 
  --dark-background: rgb(39, 166, 145);
  --dark-text-color: rgb(62, 59, 59);
  --main-font: 'Epilogue', 'Arial Narrow', Arial, sans-serif;
}

html {
  background: var(--light-background);
}
a {
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  display: block;
}

h3 {
  background: var(--dark-background);
  border-radius: 10px;
  padding: 1.2rem;
  color: white;
  font-size: var(--main-font);
}

p {
  color: var(--dark-text-color);
}

button {
  background: rgb(244, 244, 98);
  border: 1px dotted var(--dark-text-color);
  padding: 8px;
  border-radius: 10px;
  color: var(--dark-text-color);
  margin: 15px;
  width: 80px;
}

button:hover {
  opacity: 0.9;
  scale: 1.1;
}

a:hover {
  cursor: pointer;
  scale: 1.1;
  color: rgb(246, 220, 149);
}

li { 
  list-style-type: '~ ' ;
  line-height: 1.5rem;
  font-family: var(--main-font);
}

li:hover {
  opacity: 0.9;
  color: rgb(8, 102, 77);
  cursor: pointer;

}

h2 {
  color: var(--dark-text-color);
}

h1 {
  color: var(--dark-text-color);
  padding: 2rem;
  font-family: var(--main-font);
}

.swim-logo {
  height: 2.5rem;
  border-radius: 0 0 50%;
  margin-left: 1rem;
}

.swim-logo:hover {
  opacity: 0.9;
  cursor: pointer;
}

.nav-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  background: var(--dark-background);
  height: 9vh;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  margin-top: -9vh;
  overflow: hidden;
  z-index: 1;
}

.home-container {
  margin-top: 9vh;
}

.person-swim-img {
  border-radius: 10px;
}

.home-container {
  text-align: center;
  padding: 1rem;
  border-bottom: 1px dotted rgb(58, 53, 53);
}

.lessons-container {
  height: 90vh;
  margin: 10vh auto;
  text-align: center;
  width: 80vw;
  border-bottom: 1px dotted black;
}

.body-container {
  z-index: -1;
}

.swim-image {
  border-radius: 0 0 50% 0;
  width: 20vw;
}

.swim-image:hover {
  opacity: 0.9;
  scale: 1.1;
}

.lesson-options-div {
  display: flex;
  justify-content: space-around;
}

.about-us-container {
  width: 70vw;
  margin: 0 auto;
  display: flex;
  gap: 80px;
  padding: 30px;
  background: white;
  border-radius: 10px;
  color: var(--dark-text-color);
  align-items: center;
}

.contact-header {
  width: 80vw;

}

.footer-div {
  margin: 5rem auto 0;
  display: flex;
  width: 100vw;
  justify-content: center;
  gap: 5vw;
  padding: 2rem;
  background-color: var(--dark-background);
  height: 0.3rem;
  align-items: center;
}

.footer-a {
  font-size: 20px;
}

.menu-icon {
  color: white;
  padding-right: 2rem;
}